.CodingMainPage {
  height: 100vh;
  padding: 20px 50px;
  .TitleQuestion {
    height: 50px;
    border-bottom: #c1c1c1 1px solid;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .textTitle {
      font-size: 14px;
      font-weight: 700;
      text-align: left;
      letter-spacing: -0.21px;
      color: #04724d;
      opacity: 1;
      margin-left: 30px;
    }
    .TitleBtnArea {
      display: flex;
      align-items: center;
      margin-right: 20px;
      .executionleft {
        font-size: 14px;
        font-weight: 600;
        margin-right: 10px;
        color: #888888;
      }
    }
  }
  .CodingArea {
    height: calc(70% - 10px);
    display: flex;
    padding-bottom: 10px;
    justify-content: space-between;
    .QuestionArea {
      background-color: #ffffff;
      width: calc(50% - 10px);
      border-radius: 4px;
      .QuestionContent {
        overflow: auto;
        height: calc(100% - 50px);
        padding: 20px 30px;
        .QuestionMain {
          text-align: left;
          font-size: 12px;
          font-weight: 400;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
          margin-bottom: 20px;
        }
        .otherDetails {
          text-align: left;
          font-size: 10px;
          font-weight: 300;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
      }
    }
    .EditorArea {
      background-color: #ffffff;
      width: calc(50% - 10px);
      .codeEditor {
        width: 100% !important;
        height: calc(100% - 50px) !important;
        z-index: 0;
      }
    }
  }
  .OtherArea {
    height: calc(30% - 10px);
    display: flex;
    padding-top: 10px;
    justify-content: space-between;
    .TimerVideoArea {
      width: calc(50% - 10px);
      display: flex;
      justify-content: space-between;
      .VideoDiv {
        width: calc(60% - 10px);
        position: relative;
        video {
          width: 100%;
          height: 100%;
          object-fit: none;
          border-radius: 4px;
        }
        .audioDiv {
          padding: 10px 0;
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          bottom: 0px;
          position: absolute;
          background: #070707 0% 0% no-repeat padding-box;
          border-radius: 0px 0px 4px 4px;
          opacity: 0.31;
          .audio-img {
            width: 50%;
            display: flex;
            justify-content: flex-end;
          }
          .audio-analyser {
            width: 50%;
          }
          .volume {
            height: 5px;
          }
        }
      }
      .TimerDiv {
        width: calc(40% - 10px);
        background-color: #ffffff;
        border-radius: 4px;
        .TimerShow {
          display: flex;
          justify-content: center;
          align-items: center;
          height: calc(100% - 50px);
        }
      }
    }
    .IOArea {
      background-color: #ffffff;
      width: calc(50% - 10px);
      .IoHeader {
        display: flex;
        height: 50px;
        border-bottom: 1px solid #c1c1c1;
        justify-content: space-between;
        align-items: center;
        .TabSwitch {
          width: 45%;
          display: flex;
          height: 100%;
          .Tab {
            width: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 12px;
            font-weight: 600;
          }
        }
      }
      .IOtextArea {
        padding: 20px;
        height: calc(100% - 50px);
        .OutputDiv,
        .TextareaInput {
          text-align: left;
          font-size: 13px;
          font-weight: 600;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
          overflow: auto;
          width: 100%;
          height: 100%;
        }
        .OutputDiv {
          .loader {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            .MuiCircularProgress-colorPrimary {
              color: #04724d;
            }
          }
          pre {
            word-wrap: break-word;
            white-space: break-spaces;
            overflow: auto;
            width: 100%;
          }
        }
        .TextareaInput {
          border: none;
          resize: vertical;
          // background: red;
          &:focus-visible {
            outline: none;
          }
        }
      }
    }
  }
  .buttonCode {
    width: 100px;
    height: 35px;
    background: #04724d;
    color: #fff;
    font-size: 12px;
    font-weight: 600;
    text-transform: capitalize;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    img {
      margin-left: 5px;
    }
  }
  .dropdownDiv {
    margin: 0px 0px 0 20px;
    .LangauageSelect {
      width: 150px;
    }
  }
}

.CodeEditorPageSample {
  .CodeEditorMain {
    display: flex;
    justify-content: space-between;
    //   flex-direction: column;
    align-items: center;
    padding: 20px;
    .codeEditor {
      width: 49% !important;
    }
    .InputArea {
      width: 49%;
      height: 500px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .standardInput {
        background-color: #272822;
        color: #fff;
        outline: none;
        width: 100%;
        height: 45%;
        font-size: 14px;
      }
      .standardInput1 {
        background-color: #272822;
        color: #fff;
        outline: none;
        width: 100%;
        height: 45%;
        font-size: 14px;
      }
      .Codedetail {
        display: flex;
        justify-content: space-around;
      }
    }
  }
  .ButtonArea {
    display: flex;
    justify-content: center;
    .runCode {
      margin: 20px;
      width: 100px;
      background-color: #04724d;
      color: white;
      border-radius: 4px;
      text-transform: none;
      font-size: 10px;
      font-weight: 600;
      white-space: nowrap;
    }
  }
  .language-select-div {
    margin: 20px 20px 0 20px;
    display: flex;
    justify-content: flex-end;
    .language-select {
      width: 200px;
    }
  }
}
